<template>
  <div class="user-List">
    <info ref="info"></info>
    <bread></bread>
    <div class="search">
      <Row>
        <Col span="3">
          <div style="display: flex; margin-right: 5px;">
          <label style="width: 60px;margin-top: 5px;">名称：</label>
          <Input v-model.trim="name" placeholder="使用名称查询">
          </Input>
          </div>
        </Col>
         <Col span="3">
          <div style="display: flex; margin-right: 5px;">
          <label style="width: 80px;margin-top: 5px;">手机号：</label>
          <Input placeholder="使用手机号查询" v-model.trim="username"></Input>
          </div>
        </Col>
        <Col span="3">
        <DatePicker type="daterange" :options="options2" v-model="date" placeholder="选择日期"></DatePicker>
        </Col>
        <Col span="3">
        <div>
          <a href="javascript:void (0)"  @click="search" class="search_btn">查询 </a>
          <a href="javascript:void (0)"  @click="addMember" class="search_btn" v-if="power !== '0'">+ 添加用户 </a>
        </div>
        </Col>
      </Row>
    </div>
    <table>
      <tr>
        <th>名称 <span class="people_info">(点击查看详情)</span></th>
        <th>余额</th>
        <th>手机号</th>
        <th>账号数</th>
        <th>注册时间</th>
        <!--<th>状态</th>-->
        <th style="width: 200px;">操作</th>
      </tr>
      <tr class="td" v-for="(item,key) in data">
        <td><a href="javascript:void(0)" class="nickname" @click="seeInfo(item)" style="color: #2277da">{{item.nickname}}</a></td>
        <td>{{item.wallet}}</td>
        <td>{{item.username}}</td>
        <td>{{item.app_num}}</td>
        <td>{{item.register_time}}</td>
        <!--<td>{{item.register_time}}</td>-->
        <td>
          <i-switch size="large" v-model="item.is_forbidden" @on-change="chan(item,key)" true-value="0" false-value="1">
            <span slot="open">启用</span>
            <span slot="close">禁用</span>
          </i-switch>
          <img src="../../assets/garbage-icon.png" alt=""  style="vertical-align: top;cursor: pointer;margin-left: 20px;" @click="deleted(item)">
        </td>
      </tr>
    </table>
    <p v-if="data.length===0" class="td_data">
      {{msg}}
    </p>
    <!--<page></page>-->
    <div class="ter_page">
      <Page :total="num" :current="current" :page-size="currentPage" size="small" show-total show-sizer @on-change="pageNnumber" :page-size-opts="[10,20,30,40]" @on-page-size-change="pageRow">
      </Page>
    </div>
    <detailPage v-show="false"></detailPage>
    <Modal  v-model="memberModal" width="750"  title="添加会员账户">
      <form>
        <p v-for="(item,key) in items">
          <span><span class="must">*</span> {{item.name}} <strong>:</strong></span>
          <input type="text" :placeholder="item.place" v-model="item.modal" @focus="enter(key)" @blur="out(key)" @input="input(key)" class="openInput" v-if="key!==1">
          <input type="password" :placeholder="item.place" v-model="item.modal" @focus="enter(key)" @blur="out(key)" @input="input(key)" class="openInput" v-if="key===1">
          <strong v-if="item.show"><!--<Icon type="close-circled" color="#e46751" size="20" style="margin-right: 0.05rem"></Icon>--> {{item.error}} </strong>
          <strong v-if="item.right"> <Icon type="checkmark-circled" size="20" color="green"></Icon> </strong>
        </p>
        <p class="btn">
          <a href="javascript:void(0)" class="save" @click="save" >保存</a>
          <a href="javascript:void(0)" @click="cancel"  class="cancel">取消</a>
        </p>
      </form>
      <div slot="footer">
      </div>
    </Modal>
  </div>
</template>
<script>
  import bread from '../bread.vue'
  import page from '../page.vue'
  import info from '../info.vue'
  import detailPage from './detailPage.vue'
  export default {
    data() {
      return {
        items:[
          {
            name:"用户账号",
            place:"请输入用户账号",
            error:'请输入用户账号',
            modal:'',
            show:false,
            right:false
          },
          {
            name:"密码",
            place:"请输入用户密码",
            error:'请输入用户密码',
            modal:'',
            show:false,
            right:false
          },
          {
            name:"用户昵称",
            place:"请输入用户昵称",
            error:'请输入用户昵称',
            modal:'',
            show:false,
            right:false
          },{
            name:"用户姓名",
            place:"请输入用户姓名",
            error:'请输入用户姓名',
            modal:'',
            show:false,
            right:false
          },{
            name:"身份证号",
            place:"请输入身份证号",
            error:'请输入身份证号',
            modal:'',
            show:false,
            right:false
          }
        ],
        memberModal: false,
        power:'',
        msg: "正在加载....",
        data: [],
        select3: "",
        formItem: "",
        name: "",
        username:"",
        //      当前页码
        currentPage: 10,
        //      每页条数
        current: 1,
        startTime:"",
        endTime:"",
        date:'',
        //      总条数
        num: 0,
        options2: {
            shortcuts: [
              {
                text: '最近一周',
                value () {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                  return [start, end];
                }
              },
              {
                text: '最近一个月',
                value () {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                  return [start, end];
                }
              },
              {
                text: '最近三个月',
                value () {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                  return [start, end];
                }
              }
            ]
          }
      }
    },
    methods: {
      /*  添加账户*/
      addMember () {
        this.memberModal = true;
      },
      //    封装
      getUserList(currentPage, current,startTime,endTime,name,username) {
        this.post("system/Info/getAgentList", {
          page: this.current,
          limit_num:this.currentPage,
          name:name,
          username:username,
          start_time:startTime,
          end_time:endTime,
        }).then(res => {
          if(res.data.code === 10000) {
            this.data = res.data.data.info;
            this.num = res.data.data.number;
            this.data.forEach(item => {
              let time = new Date(new Date(item.register_time * 1000));
              item.is_forbidden = `${item.is_forbidden}`
              item.register_time = time.getFullYear()+'/'+ (time.getMonth()-0+1)+'/'+time.getDate() + ' ' + time.getHours()+':'+time.getMinutes();
            })
          } else {
            this.data = "暂无数据"
            const content = res.data.msg
            this.$Modal.error({
              title: "获取失败",
              content: content
            });
          }
        });
      },
//    搜索
      search(val){
        if(this.date[0]&&this.date[1]){
        this.startTime=Math.ceil(new Date(this.date[0]).getTime()/1000);
        this.endTime=Math.ceil(new Date(this.date[1]).getTime()/1000);
        }
        this.getUserList(this.name,this.username,this.startTime,this.endTime);
      },
      pageNnumber(val) {
        this.current = val
        this.getUserList(this.current,this.currentPage);
      },
      pageRow(val) {
        this.currentPage = val
        this.getUserList(this.current,this.currentPage);
      },
      seeInfo(index) {
        let url = "detailPage/" + index.id
        this.$router.push({
          path: url
        })
      },
      chan(index) {
        let custom = "custom"
        this.$Modal.confirm({
          title: '是否修改',
          content: '<p>确定要启用或关闭该用户吗</p>',
          onOk: () => {
            this.post("system/Info/forbiddenById", {
              id: index.id,
              type: custom,
              is_forbidden: index.is_forbidden
            }).then(res => {
              if (res.data.code === 10000) {
                const content = res.data.msg
                this.$Modal.success({
                  title: "修改成功",
                  content: content
                });
              } else {
                const content = res.data.msg
                this.$Modal.error({
                  title: "修改失败",
                  content: content
                });
              }
            });
          },
        });
      },
      change(index) {
        let url = '/userInfo/' + index.id
        this.$router.push(url);
      },
      deleted(index) {
        this.$Modal.confirm({
          title: '退出',
          content: '<p>确定要删除该用户吗</p>',
          onOk: () => {
            this.post("system/Info/delAgentById", {
              agent_id: index.id
            }).then(res => {
              if(res.data.code === 10000) {
                const content = res.data.msg
                this.$Modal.error({
                  title: "删除成功",
                  content: content
                });
              } else {
                const content = res.data.msg_test
                this.$Modal.error({
                  title: "删除失败",
                  content: content
                });
              }
            });

          },
          onCancel: () => {}
        });
      },
      check (val) {
        if(this.items[val].modal){
          this.items[val].show = false;
          this.items[val].right = true;
          event.target.style.borderColor = '#EAEAEA'
        }else{
          this.items[val].show = true;
          this.items[val].right = false;
          event.target.style.borderColor = '#e46751'
        }
      },
      cancelCheck (val) {
        this.items[val].show = false;
      },
//      添加用户
      publicData (val) {
        let input=document.getElementsByClassName('openInput');
        if(this.items[0].modal&&this.items[1].modal&&this.items[2].modal&&this.items[3].modal&&this.items[4].modal){
          this.post('system/Agency/add_user',{
            username: this.items[0].modal,
            password: this.items[1].modal,
            nickname: this.items[2].modal,
            agency_realname: this.items[3].modal,
            id_cart: this.items[4].modal,
          }).then(res=>{
            if(res.data.code === 10000){
              this.memberModal = false;
              this.getUserList();
              this.$Modal.success({
                title: "添加成功",
                content: res.data.msg
              });
            }else{
              this.$Modal.error({
                title: "添加失败",
                content: res.data.msg
              });
            }
          })
        }else{
          if(!this.items[0].modal){
            input[0].style.borderColor = '#e46751';
            this.items[0].show = true;
          }
          if(!this.items[1].modal) {
            input[1].style.borderColor = '#e46751';
            this.items[1].show = true;
          }
          if(!this.items[2].modal){
            input[2].style.borderColor = '#e46751';
            this.items[2].show = true;
          }
          if(!this.items[3].modal){
            input[3].style.borderColor = '#e46751';
            this.items[3].show = true;
          }
          if(!this.items[4].modal){
            input[4].style.borderColor = '#e46751';
            this.items[4].show = true;
          }
        }
      },
      save () {
        this.publicData();
      },
      cancel () {
        for(let i=0;i<this.items.length;i++){
          this.items[i].show=false;
          this.items[i].right=false;
          this.items[i].modal='';
        }
        let input=document.getElementsByClassName('openInput');
        for(let j=0;j<input.length;j++){
          input[j].style.borderColor='#EAEAEA';
        }
      },
      enter () {
        event.target.style.borderColor='#6098FF'
      },
      out (val) {
        switch (val){
          case 0:
            this.check(0);
            break;
          case 1:
            this.check(1);
            break;
          case 2:
            this.check(2);
            break;
          case 3:
            this.check(3);
            break;
          case 4:
            this.check(4);
            break;
        }
      },
      input (val) {
        switch (val){
          case 0:
            this.cancelCheck(0);
            break;
          case 1:
            this.cancelCheck(1);
            break;
          case 2:
            this.cancelCheck(2);
            break;
          case 3:
            this.cancelCheck(3);
            break;
          case 4:
            this.cancelCheck(4);
            break;
        }
      },
    },

    created() {
      this.getUserList()
    },
    mounted(){
      this.power = sessionStorage.getItem('user');
    },
    components: {
      bread,
      page,
      info,
      detailPage
    }
  }
</script>
<style scoped>
  .search_btn {
    display: inline-block;
    padding: 5px 20px;
    background-color: #6098FF;
    border-radius: 5px;
    color: white;
  }
  .search_btn:hover {
    color: white;
    text-decoration: none;
  }
  .search{
    margin-right: 20px;
    margin-bottom: 30px;
  }
  .user-List {
    width: 96%;
    margin: 0 auto;
  }
  header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .list {
    padding: 20px 0
  }
  table {
    width: 100%;
    font-size: 14px
  }
  th {
    padding: 10px 20px;
    background: #F5F5F5;
    text-align: center;
    border: 1px solid #E9ECEF;
  }
  .td>td {
    padding: 6px 10px;
    border-bottom: 1px solid #f2f2f2;
    text-align: center;
  }
  .info {
    color: #576b95;
    color: white;
    background: #93B719;
    width: 85px;
    height: 30px;
    line-height: 30px;
    display: inline-block;
    text-align: center;
    border-radius: 4px;
  }
  td {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    border: 1px solid #E9ECEF;
    background: white;
  }
  .td_data {
    width: 100%;
    padding: 20px 10px;
    text-align: center;
    border-bottom: 1px solid #f2f2f2;
  }
  a:hover {
    opacity: 0.6;
    text-decoration: none
  }
  .ter_page {
    text-align: center;
    margin-top: 40px;
  }
  p{
    padding: 9px 0;
    display: flex;
    align-items: center;
  }
  p>span{
    display: inline-block;
    width: 145px;
    text-align: right;
    margin-right: 20px
  }
  p>strong{
    display: flex;
    align-items: center;
    color: #e46751;
    font-weight: 400;
    margin-left: 10px;

  }
  .must{
    font-size: 15px;
    color: red;
    margin-right: 10px;
  }
  .btn{
    padding: 20px 168px;
  }
  .save{
    display: inline-block;
    padding: 8px 35px;
    background-color: #6098FF;
    border-radius:5px;
    color: white;
  }
  .cancel{
    margin-left: 15px;
    display: inline-block;
    padding: 7px 34px;
    border: 1px solid #777777;
    border-radius: 5px;
    color:#777777;
  }
  .cancel:hover{
    background:#799EE3 ;
    color: white;
    text-decoration:none;
    border: none;
  }
  input{
    display: inline-block;
    padding: 8px 10px;
    border: 1px solid #EAEAEA;
    width: 400px;
    border-radius: 5px;
    background-color: #FFFFFF;
  }
  .people_info{
    font-size: 1em;
    font-weight: normal;
    color: #444;
    letter-spacing: 0.2em;
    margin-left: 5px;
  }
  .nickname{
    color: black;
  }
  .nickname:hover{
    color: #6098FF;
  }
</style>



