<template>
  <div>
    <div class="box" v-if="show">
      <Icon type="checkmark" size="25"></Icon>
      <p>删除成功</p>
    </div>
  </div>
</template>
<script>
export default{
  data(){
    return {
      show:false
    }
  }
}
</script>
<style scoped>
.box{
  width: 1.5rem;
  height: 0.8rem;
  background-color: #262626;
  border-radius:0.1rem;
  color: white;
  text-align: center;
  position: absolute;
  opacity: 0.8;
  left: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  animation: 1s my;
}
  p{
    color: white;
  }
  @keyframes my {
    0%{
      top:0
    }
    100%{
      top:1rem
    }
  }
</style>



